import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Grid from '@material-ui/core/Grid'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import Typography from '@material-ui/core/Typography'

// Components
import PageFooter from './../../../Components/PageFooter/PageFooter'
import SearchTextComponent from './../../../Components/FormComponents/SearchTextComponent/SearchTextComponent'
import { CustomTable as Table } from './../../../Components/CustomTable'

// Services
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'

// Utilities
import pick from 'lodash/pick'
import upperFirst from 'lodash/upperFirst'
import moment from 'moment'
import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './LetterTemplate.module.scss'

export default function LetterTemplate () {
  const history = useHistory()
  const [data, setData] = React.useState({
    header: HEADERS,
    body: [],
    count: 0,
    limit: 10,
    order: true,
    orderBy: 'due_date',
    page: 1,
    sort: '',
    search: ''
  })

  const getData = (params = {}) => {
    LoadingService.loading(true)
  }

  return (
    <>
      <Grid item xs={12}>
        <Grid container className={classes.search}>
          <Grid item xs>
            <SearchTextComponent
              placeholder='Search from the list of letter templates'
              displaySettings
              displaySettingsTitle={`Submitted Date Range*`}
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <Grid container>
          <Grid item xs>
            <Table
              hasLink
              data={data}
              onDataListChange={getData}
              setData={setData}
              showTextField
              body={template =>
                template.map((template, index) => (
                  <TableRow key={`template-letter-table-row-${index}`}>
                    <TableCell id={`template-letter-table-cell-letter-type-${index}`}>
                      {upperFirst(template.letter_type)}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-template-name-${index}`}>
                      {template.template_name}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-category-${index}`}>
                      {template.category}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-description-${index}`}>
                      {template.description}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-author-${index}`}>
                      {template.author}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-last-updated-${index}`}>
                      {moment(template.date_updated).format('ll')}
                    </TableCell>
                    <TableCell id={`template-letter-table-cell-action-${index}`}>
                      <Link to={`/`}>
                        <Typography
                          id={`template-letter-view-${index}`}
                          variant='caption'
                          className='text-link underline'
                        >
                            generate
                        </Typography>
                      </Link>
                    </TableCell>
                  </TableRow>
                ))
              }
            />
          </Grid>
        </Grid>
      </Grid>

      <Grid item xs={12}>
        <PageFooter
          showCollectionMaintenanceBtn
          handleCreateLetterTemplate={() => history.push('/collection/maintenance/create-letter-template')}
        />
      </Grid>
    </>
  )
}

const HEADERS = [
  {
    id: 'as-letter-type',
    numeric: false,
    disablePadding: false,
    name: 'Letter Type',
    sortField: 'letter_type',
    match: 'any'
  },
  {
    id: 'as-template-name',
    numeric: false,
    disablePadding: false,
    name: 'Template Name',
    sortField: 'template_name',
    match: 'any'
  },
  {
    id: 'as-category',
    numeric: false,
    disablePadding: false,
    name: 'Category',
    sortField: 'category',
    match: 'any'
  },
  {
    id: 'as-description',
    numeric: false,
    disablePadding: false,
    name: 'Description',
    sortField: 'description',
    match: 'any'
  },
  {
    id: 'as-author',
    numeric: false,
    disablePadding: false,
    name: 'Author',
    sortField: 'author',
    match: 'any'
  },
  {
    id: 'as-last-update',
    numeric: false,
    disablePadding: false,
    name: 'Last Update',
    sortField: 'date_updated',
    match: 'any'
  },
  {

  }
]
